"use client"

import { cn } from "@/lib/utils"
import Ribbons from "@/components/ui/ribbons"
import SimpleRotatingText from "@/components/ui/simple-rotating-text"
import ScrollStack, { ScrollStackItem } from "@/components/ui/scroll-stack"

export function MissionSection() {
    return (
        <section className="relative bg-black text-white py-20 overflow-hidden min-h-screen">
            {/* Background Image */}
            <div className="absolute inset-0 z-0">
                <div
                    className="absolute inset-0 bg-cover bg-center bg-no-repeat opacity-70"
                    style={{ backgroundImage: "url('/our_mission.jpg')" }}
                />
            </div>

            {/* Ocean Ribbons Background */}
            <div className="absolute inset-0 z-[1] pointer-events-none opacity-40">
                <Ribbons
                    colors={['#22d3ee', '#3b82f6', '#8b5cf6', '#06b6d4']}
                    baseThickness={20}
                    enableFade={true}
                    backgroundColor={[0, 0, 0, 0]}
                />
            </div>

            {/* Background Gradient Overlay */}
            <div className="absolute inset-0 bg-gradient-to-b from-ocean-dark/80 to-black/90 pointer-events-none z-[2]" />

            {/* Content Container */}
            <div className="relative z-10 container mx-0 px-6 space-y-12">

                {/* "Our Mission" Header */}
                <div className="text-center">
                    <h2 className="text-6xl md:text-8xl font-bold tracking-widest uppercase text-gray-300 drop-shadow-[0_0_20px_rgba(255,255,255,0.3)]">
                        Our Mission
                    </h2>
                </div>

                {/* "Is" */}
                <div className="text-center">
                    <p className="text-3xl md:text-5xl font-light text-gray-400">
                        Blend
                    </p>
                </div>

                {/* "To Blend" + RotatingText */}
                <div className="text-center">
                    <h3 className="text-4xl md:text-7xl font-light text-gray-200 leading-tight flex items-center justify-center gap-4 flex-wrap">
           
                        <SimpleRotatingText
                            texts={["Art", "Science", "Community Engagement"]}
                            interval={2500}
                            staggerDuration={0.03}
                            className="text-4xl md:text-6xl text-white drop-shadow-[0_0_15px_rgba(34,211,238,0.5)] font-light"
                        />
                    </h3>
                </div>

                {/* "To" */}
                <div className="text-center mb-16">
                    <p className="text-3xl md:text-5xl font-light text-gray-400">
                        To
                    </p>
                </div>

            </div>

            {/* ScrollStack with Mission Points */}
            <div className="relative z-10 mt-20">
                <ScrollStack
                    className="w-full"
                    itemDistance={150}
                    itemScale={0.03}
                    itemStackDistance={30}
                    stackPosition="20%"
                    baseScale={0.85}
                    useWindowScroll={true}
                >
                    <ScrollStackItem itemClassName="bg-cyan-500/20 backdrop-blur-md border border-cyan-500/30">
                        <div className="flex items-center justify-center h-full">
                            <p className="text-2xl md:text-3xl text-gray-200 font-light text-center">
                                Inspire deep-sea awareness in children and youth
                            </p>
                        </div>
                    </ScrollStackItem>

                    <ScrollStackItem itemClassName="bg-purple-500/20 backdrop-blur-md border border-purple-500/30">
                        <div className="flex items-center justify-center h-full">
                            <p className="text-2xl md:text-3xl text-gray-200 font-light text-center">
                                Empower local communities with knowledge
                            </p>
                        </div>
                    </ScrollStackItem>

                    <ScrollStackItem itemClassName="bg-blue-500/20 backdrop-blur-md border border-blue-500/30">
                        <div className="flex items-center justify-center h-full">
                            <p className="text-2xl md:text-3xl text-gray-200 font-light text-center">
                                Advocate for the protection of deep-sea ecosystems
                            </p>
                        </div>
                    </ScrollStackItem>

                    <ScrollStackItem itemClassName="bg-red-500/20 backdrop-blur-md border border-red-500/30">
                        <div className="flex items-center justify-center h-full">
                            <p className="text-2xl md:text-3xl text-gray-200 font-light text-center">
                                Counter the rising threat of deep-seabed mining
                            </p>
                        </div>
                    </ScrollStackItem>
                </ScrollStack>
            </div>

        </section>
    )
}
